import React from 'react';
import '../styles/aboutMe.css';

const AboutMe = () => {
	return (
		<div className='aboutme section-container'>
			<h1 className='section-h1'>O mnie</h1>
			<p>
				Cze, jestem Karolina i jestem studentk drugiego roku informatyki nastawion na zdobywanie
				dowiadczenia zawodowego jako front-end developer. Szukam stanowiska,
				ktre umoliwi mi rozwj moich umiejtnoci i kwalifikacji.
			</p>
		</div>
	);
};

export default AboutMe;
